*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
  color: gold;

}
.navbar {
  background-color: black;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 20;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 30px;
  margin-right: 1rem;
  font-family: "Montserrat";
  font-size: 18px;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #FF9936;
}

.nav-item .active {
  color: #FF9936;}

.nav-icon {
  display: none;
}

@media screen and (max-width: 970px) {
  .nav-menu {
    display: none;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 40px;
    margin-left: -90px;
    opacity: 1;
    transition: all 0.5s ease;
    font-size: 14px;
  }

  .nav-menu.active {
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    display: flex;
  }
  .nav-item .active {
    color: #FF9936;
    border: none;
  }
  
  .nav-item {
    height: 50px;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff; 
  }
}



.highcharts-background {
    fill: #000000;
     
}

.highcharts-candlestick-series .highcharts-point-down {
    fill:  #ff5e00;
    stroke:  #ff5e00;
}

.highcharts-candlestick-series .highcharts-point-up {
    stroke: #35bd00;
    fill: #35bd00;
}


.highcharts-color-1 {
    fill: #90ed7d;
    
}
.highcharts-color-0  {
    fill: red;
}

.highcharts-yaxis .highcharts-axis-line {
    stroke-width: 2px;
   
}

